<template>

    <form class="credit-selection-form"
          :class="{'disabled': disabled}"
          @submit.prevent="routeCredit">

        <div class="credit-selection-form-row">

            <div class="credit-selection-form-col">

                <div class="form-group-2 with-icon">
                    <label class="form-label-2">Сумма кредита, руб</label>
                    <div class="range-slider-group">
                        <span class="range-slider-fill">
                            <span :style="{width: this.disabled ? 0 : `${getSumFill}%`}"/>
                        </span>
                        <input type="tel"
                               class="form-control no-label"
                               v-mask="decimalMask"
                               v-model="sum"
                               @focus="focusCreditSum"
                               @blur="blurCreditSum">
                        <vue-slider v-if="!this.disabled"
                                    v-model="sum"
                                    :min="orderStep1 ? ( currentTariff ? currentTariff.minSum : 0 ) : creditSum.min"
                                    :max="orderStep1 ? ( currentTariff ? ( currentTariff.maxSum ? currentTariff.maxSum : 100000000 ) : 0 ) : creditSum.max"/>
                    </div>
                    <span class="form-group-icon">
                        <span class="icon-ruble"></span>
                    </span>
                </div>

            </div>

            <div class="credit-selection-form-col">
                <div class="form-group-2">
                    <label class="form-label-2">Срок</label>
                    <div class="range-slider-group">
                        <span class="range-slider-fill">
                            <span :style="{width: this.disabled ? 0 : `${getTermFill}%`}" />
                        </span>
                        <label class="range-slider-label">
                            <div v-if="rangeFocus"
                                 class="range-slider-input">
                                <input type="tel"
                                       class="form-control no-label"
                                       v-mask="decimalMask"
                                       :style="{'width' : `${hiddenTextWidth}px`}"
                                       @blur="termMinMaxCheck"
                                       v-model="term">
                                <span ref="inputHiddenText" class="hidden-text"><span>{{term}}</span></span>
                                <span class="range-slider-input-text">{{parseDate(term)}}</span>
                            </div>
                            <div v-else
                                 class="range-slider-nofocus-text"
                                 @click="rangeFocus = true">{{termModify}}
                            </div>
                        </label>
                        <vue-slider v-if="!this.disabled"
                                    v-model="term"
                                    :min="orderStep1 ? ( currentTariff ? ( currentTariff.minTerm ? currentTariff.minTerm : 0 ) : 0 ) : creditTerm.min"
                                    :max="orderStep1 ? ( currentTariff ? currentTariff.maxTerm : 200 ) : creditTerm.max"/>
                    </div>
                </div>
            </div>

            <div class="credit-selection-form-col wide">

                <div class="credit-selection-target">
                    <div class="form-group-2">
                        <label class="form-label-2">Цель кредита</label>
                        <v-select
                            v-if="orderStep1 ? this.creditTargetOptionsForTariff.length > 0 : true"
                            label="title"
                            :value="creditTarget"
                            :reduce="target => target.id"
                            :options="orderStep1 ? getCreditTargetOptionsForTariff : creditTargetOptions"
                            :searchable="false"
                            :key="selectUniqueKey"
                            @option:selecting="updateCreditTarget"/>
                    </div>
                </div>

            </div>

        </div>

        <div v-if="creditInfo"
             class="credit-selection-info">
            <div class="credit-selection-info-item">Ставка: <span>{{rate}}%</span></div>
            <div class="credit-selection-info-item">Комиссия: <span>{{commission | numberFilter}} руб.</span></div>
            <div class="credit-selection-info-item">Ежемесячный платеж: <span>{{monthlyPayment | numberFilter}} руб.</span></div>
        </div>

        <div class="credit-selection-footer">

            <div
                v-if="currentTariff.line"
                class="credit-selection-footer-credit-line">

                <FormGroupSwitchCheckbox v-model="creditLine"
                                         @change="changeCreditLine">
                    Кредитная линия
                </FormGroupSwitchCheckbox>

            </div>

            <div class="credit-selection-footer-insure-life">

                <FormGroupSwitchCheckbox v-model="creditInsureLife"
                                         @change="changeInsure">
                    Застраховать жизнь {{ company.ul ? 'директора' : 'предпринимателя' }}*<br><span class="green">Вероятность одобрения +30%, Снижение ставки - 2%</span>
                </FormGroupSwitchCheckbox>

            </div>

            <div class="form-submit">
                <button v-if="!disabled && orderStep1"
                        type="submit"
                        class="btn"
                        :disabled="load"
                        :class="{'loading': load}">Продолжить</button>
            </div>

        </div>

        <div class="credit-selection-info-text">
            <p>*Если срок кредитного договора более срока договора страхования, требуется пролонгация договора страхования на весь срок кредита.</p>
        </div>

    </form>

</template>

<script>

    import createNumberMask from 'text-mask-addons/dist/createNumberMask';
    import VueSlider from 'vue-slider-component'
    import {mapState, mapGetters} from 'vuex'
    import 'vue-slider-component/theme/default.css'
    import FormGroupSwitchCheckbox from "@/components/Inputs/FormGroupSwitchCheckbox";

    const currencyMask = createNumberMask({
        prefix: '',
        allowDecimal: true,
        includeThousandsSeparator: true,
        allowNegative: false,
        thousandsSeparatorSymbol: ' '
    });

    export default {
        name: 'Home',
        props: {
            orderStep1: Boolean,
            disabled: {
                type: Boolean,
                default: false
            },
            creditInfo: Object
        },
        components: {
            FormGroupSwitchCheckbox,
            VueSlider,
        },
        data: () => ({
            decimalMask: currencyMask,
            orderId: '',
            hiddenTextWidth: 0,
            rangeFocus: false,
            load: false,
            selectUniqueKey: (new Date()).toString()
        }),
        computed: {
            ...mapState({
                order: state => state.createOrder.orderFullInfo,
                creditSum: state => state.createOrder.creditSum,
                creditTarget: state => state.createOrder.creditTarget,
                creditTerm: state => state.createOrder.creditTerm,
                tariffs: state => state.tariffs.tariffs,
                company: state => state.users.company
            }),
            currentTariff () {
                if (this.orderStep1) {
                    return this.tariffs.filter(item => item.name === this.order.order.name)[0]
                } else {
                    return {}
                }
            },
            ...mapGetters({
                creditTargetOptions: 'getTarrifsTargets',
                creditTargetOptionsForTariff: 'getTarrifsTargetsForTariff',
            }),
            getCreditTargetOptionsForTariff () {
                return this.creditTargetOptionsForTariff(this.order.order.tariff_id)
            },
            creditInsureLife: {
                get() {
                    return this.$store.state.createOrder.creditInsureLife
                },
                set(value) {
                    this.$store.commit('updateCreditInsureLife', value)
                }
            },
            creditLine: {
                get() {
                    return this.$store.state.createOrder.creditLine
                },
                set(value) {
                    this.$store.commit('updateCreditLine', value)
                }
            },
            sum: {
                get() {
                    return this.disabled ? 0 : parseInt(this.creditSum.value.toString().replace(/\s/g, ''))
                },
                set(value) {
                    this.$store.commit('setCreditSum', value)
                }
            },
            term: {
                get() {
                    let val = parseInt(this.creditTerm.value.toString().replace(/\s/g, ''))
                    return this.disabled || isNaN(val) ? 0 : val
                },
                set(value) {
                    this.$store.commit('setCreditTerm', value)
                }
            },
            termModify() {
                let term = parseInt(this.term.toString().replace(/\s/g, ''))
                let text = ''
                if ( this.term < 12 ) {
                    text = `${term} ${this.$options.filters.declensionNumbers(term, ['месяц', 'месяца', 'месяцев'])}`
                }
                else {
                    let year = parseInt(term / 12),
                        months = term % 12,
                        yearText = `${year} ${this.$options.filters.declensionNumbers(year, ['год', 'года', 'лет'])}`,
                        monthsText = months !== 0 ? ` ${months} ${this.$options.filters.declensionNumbers(months, ['месяц', 'месяца', 'месяцев'])}` : '';
                    text = yearText + monthsText
                }
                return text
            },
            commission () {
                if (this.currentTariff) {
                    let res = 0
                    let sum = parseInt(this.creditSum.value.toString().replace(/\s+/g, ''))
                    res = sum * this.currentTariff.commissionPercent / 100
                    return res > this.currentTariff.minCommission ? res : this.currentTariff.minCommission
                } else {
                    return 0
                }
            },
            rate () {
                if (this.currentTariff) {
                    return this.creditInsureLife ? this.currentTariff.rateWithInsurance : this.currentTariff.rateWithoutInsurance
                } else {
                    return 0
                }
            },
            monthlyPayment () {
                let res = 0
                if (this.currentTariff) {
                    let rate = this.creditInsureLife ? this.currentTariff.rateWithInsurance : this.currentTariff.rateWithoutInsurance
                    let sum = parseInt(this.creditSum.value.toString().replace(/\s+/g, ''))
                    let term = parseInt(this.creditTerm.value.toString().replace(/\s+/g, ''))
                    const total = sum / term + sum * (rate / 100) / 12
                    res = parseFloat(total.toFixed(2))
                }

                return isFinite(res) ? res : '0'
            },
            getSumFill () {
                if (this.currentTariff) {
                    return ( 100 * this.sum / ( this.orderStep1 ? ( this.currentTariff.maxSum ? this.currentTariff.maxSum : 100000000 ) : this.creditSum.max ) )
                } else {
                    return 0
                }
            },
            getTermFill () {
                if (this.orderStep1) {
                    if (this.currentTariff) {
                        return 100 * (this.creditTerm.value-this.currentTariff.minTerm) / (this.currentTariff.maxTerm - this.currentTariff.minTerm)
                    } else {
                        return 0
                    }
                } else {
                    return 100 * (this.creditTerm.value-this.creditTerm.min) / (this.creditTerm.max - this.creditTerm.min)
                }
            }
        },
        watch: {
            term() {
                setTimeout(() => {
                    this.hiddenTextWidth = this.$refs.inputHiddenText ? this.$refs.inputHiddenText.offsetWidth : 0
                }, 100)
            },
            creditTargetOptions(newVal) {
                if (!this.orderStep1) {
                    this.$store.commit('setCreditTarget', this.creditTargetOptions[0].id)
                }
            }
        },
        methods: {
            routeCredit() {
                this.load = true
                if (!this.disabled) {
                    this.$store.dispatch('updateOrder', this.orderId).then(() => {
                        this.$store.dispatch('nextStep').then(() => {
                            this.load = false
                        }).catch(() => {
                            this.load = false
                        })
                    }).catch(() => {
                        this.load = false
                    })
                }
            },
            focusCreditSum(e) {
                let val = e.target.value
                if ( val === '0' ) {
                    e.target.value = ''
                }
            },
            blurCreditSum(e) {
                let val = e.target.value.length > 0 ? e.target.value : '0'
                e.target.value = val
                let sum = parseInt(val.toString().replace(/\s/g, '')),
                    min = this.creditSum.min,
                    max = this.creditSum.max;
                if ( sum > max ) {
                    sum = max
                }
                if ( sum < min ) {
                    sum = min
                }
                this.$store.commit('setCreditSum', sum)
            },
            updateCreditTarget(e) {
                this.$store.commit('setCreditTarget', e.id)
            },
            changeInsure(val) {
                this.$store.commit('setCreditInsureLife', val)
            },
            changeCreditLine(val) {
                this.$store.commit('setCreditLine', val)
            },
            parseDate(date) {
                return this.$options.filters.declensionNumbers(parseInt(date.toString().replace(/\s/g, '')), ['месяц', 'месяца', 'месяцев'])
            },
            termMinMaxCheck() {
                let term = parseInt(this.term.toString().replace(/\s/g, '')),
                    min = this.creditTerm.min,
                    max = this.creditTerm.max;
                if ( term > max ) {
                    term = max
                }
                if ( term < min ) {
                    term = min
                }
                this.rangeFocus = false
                this.term = term
            },

        },
        mounted() {
            this.orderId = parseInt(this.$route.params.id)
            this.hiddenTextWidth = this.$refs.inputHiddenText ? this.$refs.inputHiddenText.offsetWidth : 0
            if (!this.orderStep1 && this.creditTargetOptions[0]) {
                this.$store.commit('setCreditTarget', this.creditTargetOptions[0].id)
            }
        }
    }
</script>
